            <v-btn flat class="hidden-sm-and-down">The campus cinema</v-btn>
            <v-spacer class="hidden-sm-and-down"></v-spacer>
            <span class="icon-top">
            <i class="material-icons" title="Edit details">person</i>
            <i class="material-icons" title="Logout">settings_power</i>
             </span>
                        
        </v-toolbar>
    </span>
