<script>
export default {
    name: 'AppNavigation',
    data() {
        return {
            appTitle: 'Exodus',
            drawer: true,
            items: [
                { title: 'Dashboard',route:'/home'},
                { title: 'Tickets',route:'/tickets' },
                { title: 'Refreshment' ,route:'/refreshments'},
                { title: 'Rights' ,route:'/rights'},
                { title: 'Campus' ,route:'/campus'},
                { title: 'Watch Now' ,route:'/watch'},
                { title: 'Staffs' ,route:'/staff'},
                { title: 'Logout' ,route:'/'}
            ]
        };
    }
};
</script>

<style scoped>
.icon-top i{
	padding:5px;
}
a:visited,a{
	text-decoration:none;
	color:white;
}
</style>